$('.card-input').mask('9999 9999 9999 9999', { autoclear: false }, { placeholder: '                   ' });


function feedbackSlider() {
    var swiper = new Swiper('.feedback .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 32,
        loop: true,
        navigation: {
            nextEl: '.feedback .swiper-button-next',
            prevEl: '.feedback .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            992: {
                slidesPerView: 2,
            },
            1400: {
                slidesPerView: 3,
            },
        }
    })
}


feedbackSlider()



$(".rating__item").click(function() {
    let ratingItem = parseInt($(this).attr("data-rating-value"))
    $(this).parent(".rating").attr("data-total-rating", ratingItem)
})
