let languages = document.querySelectorAll(".lang__show")
let languagesDropdown = document.querySelectorAll(".lang__hidden")


if(languages.length>0){
    languages.forEach(language => {
        language.onclick = function () {
            language.closest('.lang').classList.add("_active")
        }
    })
}

let langClickCount = 0

languagesDropdown.forEach(dropdown => {
    document.addEventListener( 'click', (e) => {
        langClickCount++
        const withinBoundaries = e.composedPath().includes(dropdown);
        if ( ! withinBoundaries && langClickCount>1) {
            dropdown.closest('.lang').classList.remove("_active")
            langClickCount = 0
        }
    })
})


let burger = document.querySelector(".burger")
let body = document.querySelector("body")

if(burger){
    burger.onclick = function () {
        body.classList.toggle("_open")
    }
}

